



















































































  import rewardRule from '../../components/rewardRule.vue'
  
	export default {
    components: {
      rewardRule
    },
		data() {
			return {
        advLists: [],
        lists: [],
        loanType: 0,
        currentPage: 1, // 当前页
        maxPage: 1, // 最大页
        loading: false, // 加载中
        loanId: '',
        rewardData: {}
			};
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      },
      isRole() {
        return this.userInfo.role >= 1
      },
      isLoad() {
        return parseInt(this.maxPage) <= parseInt(this.currentPage) && this.lists.length > 0
      }
    },
		onLoad() {
      // banner
      this.$http({
        url: this.$apis.adverts,
        method: 'GET',
        data: {
          place: 7
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.advLists = data.data
          }
        }
      })
      // 列表
      this.updateLists()
		},
    methods: {
      handleStr(str) {
      	return str.split(',')
      },
      updateLists() {
        if (this.loading) return
        this.loading = true
        if (parseInt(this.currentPage) <= 1) {
          this.lists = []
        }
        this.$http({
          url: this.$apis.loanList,
          method: 'GET',
          data: {
            page: this.currentPage,
            type: this.loanType
          },
          cb: (err, data) => {
            if (!err && data.success === true) {
              this.currentPage = data.data.current_page
              this.maxPage = data.data.last_page
              let oldArr = this.lists
              let newArr = data.data.data
              newArr.forEach(item => {
                this.$set(item, 'tagArr', this.handleStr(item.tag_ids))
              })
              this.lists = oldArr.concat(newArr)
            } else {
              uni.showToast({
              	title: '获取贷款列表失败',
                icon: 'none'
              })
            }
            this.loading = false
          }
        })
      },
      tabClick(type) {
        if (this.loanType === type) return
        if (this.loading) {
          uni.showToast({
          	title: '正在加载数据',
            icon: 'none'
          })
          return
        }
        this.currentPage = 1
        this.maxPage = 1
        this.loanType = type
        this.updateLists()
      },
      scrollUpdate() {
        if (!this.loading && !this.isLoad && this.lists.length > 0) {
          this.currentPage ++
          this.updateLists()
        }
      },
      gotoDetails(id) {
        this.$navigateTo({
        	url: '/pages/loan/details?loanId=' + id
        })
      },
      showRule(item) {
        this.loanId = item.id
        if (item.commission_type === 0) {
          this.rewardData = {
            show: true,
            total: item.max_price,
            conditionTitle: item.clearing_title,
            conditionDec: item.clearing_info,
            type: 1,
            name: 'dk',
            frPercent: item.rate
          }
        } else {
          this.rewardData = {
            show: true,
            total: item.rate,
            conditionTitle: item.clearing_title,
            conditionDec: item.clearing_info,
            name: 'dk'
          }
        }
      },
      gotoHandle() {
        uni.setStorageSync('createLoan', true)
        this.$navigateTo({
        	url: './details?loanId=' + this.loanId
        })
      },
      gotoOrder: function () {
        uni.setStorageSync('orderIndex', 1)
        this.$navigateTo({
          url: '/pages/mine/order?nav=1'
        })
      }
    }
	}
